"use client";

import s from "./page.module.scss";
import questionsJson from "@/data/questions.json";
import Timeline from "@/components/Timeline/Timeline";
import QuestionItem from "@/components/QuestionItem/QuestionItem";
import Filter from "@/components/Filter/Filter";
import { useState } from "react";
import clsx from "clsx";

export default function PatientQuestions() {
  const [questions, setQuestions] = useState(questionsJson);
  const groupedQuestions = Object.groupBy(questions, (question) =>
    question.period.toString()
  );

  return (
    <main className={s.main}>
      <Timeline isPatientPage={true} />

      <div className={clsx(s.content, "page-content")}>
        <div className={s.content__header}>
          <h1 className="heading-xl">
            Patient <div className="bold">Questions</div>
          </h1>
          <p className={s.content__subtitle}>
            Common questions from patients diagnosed with VTE, arranged by
            relevant time points in the patient journey and further categorized
            by topic. This 'patient perspective' can help guide your
            conversations with patients, addressing their needs and concerns
            effectively.
          </p>
        </div>
        <div className={s.content__main} id="questions-group">
          {Object.entries(groupedQuestions).map(([title, questions], index) => (
            <QuestionItem
              key={title}
              title={title}
              index={index}
              questions={questions}
            />
          ))}
        </div>
      </div>

      <Filter
        onTopicSelect={(label) => {
          if (label == "") {
            setQuestions(questionsJson);
            return;
          }
          const newQuestions = questionsJson.filter((question) => {
            return (
              question.primary === label ||
              question.secondary1 === label ||
              question.secondary2 === label
            );
          });
          setQuestions(newQuestions);
        }}
      />
    </main>
  );
}
